import React from 'react'
import './cluster-edge.css'

interface ClusterEdgeProps {
    x1: number
    y1: number
    x2: number
    y2: number
    weigth: number
    clusterWidth: number
}

export default class ClusterEdge extends React.Component<ClusterEdgeProps, {}> {

    render() {
        let top:number, left:number, wid:number, hei:number, thickness = this.props.weigth;
        
        if (this.props.x1 > this.props.x2) {
            left = this.props.x2
            wid = this.props.x1 - this.props.x2
        }
        else {
            left = this.props.x1
            wid = this.props.x2 - this.props.x1
        }

        if (this.props.y1 > this.props.y2) {
            top = this.props.y2
            hei = this.props.y1 - this.props.y2
        }
        else {
            top = this.props.y1
            hei = this.props.y2 - this.props.y1
        }

        let x1:number = 0 + thickness, y1:number = hei, x2:number = wid, y2:number = 0 + thickness;
        if ((this.props.y1 > this.props.y2 && this.props.x1 > this.props.x2) ||
            (this.props.y1 < this.props.y2 && this.props.x1 < this.props.x2)) {
            y1 = 0 + thickness;
            y2 = hei;
        }


        const st: React.CSSProperties ={
            top: top + 30,
            left: left + this.props.clusterWidth / 2
        }

        const curvature = 20
        return(
            <svg style={st} className="svgWrapper" width={wid+thickness+curvature} height={hei+thickness+curvature}>
                <path d={`M ${x1},${y1} C ${x1+curvature},${y1+curvature} ${x2+curvature},${y2+curvature} ${x2},${y2}`} strokeWidth={thickness} fill="none" stroke="black"/>
                {/* <line x1={x1} y1={y1} x2={x2} y2={y2} strokeWidth={thickness*2} stroke="black"/> */}
            </svg>
        )
    }
}
