import React from 'react'
import { connect } from 'react-redux'
import './draggable-canvas.css'
import { State, SongFull } from '../store/types'
// import SpotifyPlayer from 'react-spotify-web-playback';
import { isUndefined } from 'util'
import './styles.css'
import { setPaused } from '../store/actions'


interface PlayerProps {
    accessToken?: string
    songToPlay?: SongFull
    paused: boolean
    setPaused: Function
}

interface PlayerState {
}

class Player extends React.Component<PlayerProps, PlayerState> {
    playerRef: any
    constructor(props: any) {
        super(props);
        
        this.playerRef = React.createRef();
    }

    componentDidUpdate() {
        if (this.props.paused) {
            this.playerRef.current.pause()
        }
        else {
            this.playerRef.current.play()
        }
    }

    render() {
        if (isUndefined(this.props.accessToken)) {
            return(
                <div></div>
            )
        }
        else {
            let mp3Link = ''
            let description = 'Chose a song to play a preview'
            if (this.props.songToPlay) {
                description = this.props.songToPlay.artistsIds.join(', ')
                description += ' :: '
                description += this.props.songToPlay.name
                mp3Link = this.props.songToPlay.previewLink
            }
            
            return (
                <div className="player">
                    <div className="flex1">
                        <h3>{description}</h3>
                    </div>
                    <audio onPlay={() => this.props.setPaused(false)} 
                        onPause={() => this.props.setPaused(true)} 
                        ref={this.playerRef} 
                        autoPlay controls src={mp3Link} className="playerControls"
                    >
                        Your browser does not support the
                        <code>audio</code> element.
                    </audio>
                </div>
            )
        }
    }
}


const mapStateToProps = (state: State) => {
    return {
        paused: state.paused,
        accessToken: state.accessToken,
        songToPlay: state.songToPlay
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPaused: (paused:boolean) => dispatch(setPaused(paused)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Player)
